import request from '@/utils/request'
export function getProductDetail (goodsId) {
  return request.get('/goods/detail', {
    params: {
      goodsId
    }
  })
}
export function getCommentsNumber (goodsId) {
  return request.get('/comment/total', {
    params: {
      goodsId
    }
  })
}
export function getAllCommments (goodsId, limit) {
  return request.get('/comment/listRows', {
    params: {
      goodsId,
      limit
    }
  })
}
