<template>
  <div class="order-checkout">
    <!-- 顶部导航栏 -->
    <van-nav-bar
    title="提交订单"
    left-text="返回"
    left-arrow
    @click-left="$router.go(-1)"
    />

    <!-- 收货地址 -->
    <section class="address" @click="handleClickAddress">
      <h2 class="section-title">收货地址</h2>
      <p class="address-details">{{ address.first }}</p>
      <p class="address-details">{{ address.second }}</p>
      <van-icon name="arrow" color="rgb(137 137 137 / 78%)" size="40" />
    </section>
    <!-- 商品列表 -->
    <div class="item" v-if="buynow.product.goodsId!==undefined">
          <img :src="buynow.product.goods_image" :title="buynow.product.goods_name">
          <div class="product">
          <div class="des">{{buynow.product.goods_name}}</div>
          <span class="price">￥{{buynow.product.goods_price_min}}</span>
          <span class="quatity">x{{buynow.goodsNum}}</span>
        </div>
    </div>
    <div class="item" v-show="buynow.product.goodsId===undefined" v-for="item in selectedItems" :key="item.goods_id">
          <img :src="item.goods.goods_image" :title="item.goods.goods_name">
          <div class="product">
          <div class="des">{{item.goods.goods_name}}</div>
          <span class="price">￥{{item.goods.skuInfo.goods_price}}</span>
          <span class="quatity">x{{item.goods_num}}</span>
        </div>
    </div>

    <!-- 订单总金额 -->
    <section class="order-summary">
      <h2 class="section-title">订单总金额</h2>
      <p class="total-amount">总金额: {{ total }} 元</p>
      <p class="shipping-fee">配送费用: {{ shippingFee }} 元</p>
      <van-coupon-cell
      :coupons="coupons.available"
      :chosen-coupon="coupons.chosenCoupon"
      @click="showList = true"
      />
      <!-- 优惠券列表 -->
      <van-popup
        v-model="showList"
        round
        position="bottom"
        style="height: 90%; padding-top: 4px;"
      >
      <van-coupon-list
        :coupons="coupons.available"
        :chosen-coupon="coupons.chosenCoupon"
        :disabled-coupons="coupons.disabled"
        @change="onChange"
        @exchange="onExchange"
      />
      </van-popup>
        <p class="payable-amount">实付金额: {{ payableAmount }} 元</p>
      </section>

    <!-- 支付方式 -->
    <div style="text-align:center;font-weight:bold">支付方式</div>
    <van-cell is-link title="支付方式" @click="payment.showPayment = true" />
    <van-action-sheet v-model="payment.showPayment" :actions="payment.actions" @select="onSelect" />

    <!-- 卖家留言 -->
    <section class="seller-message">
      <h2 class="section-title">卖家留言</h2>
      <textarea v-model="sellerMessage" class="message-input" placeholder="请输入您的留言"></textarea>
    </section>

    <!-- 提交订单按钮 -->
    <footer class="footer">
      <button @click="submitOrder" class="submit-button">提交订单</button>
    </footer>
  </div>
</template>

<script>
import { getDefaultAddressApi, getAddressDetailApi } from '@/api/addresses'
import { submitOrderInCartApi, submitOrderBuyNow } from '@/api/order'
import { mapGetters } from 'vuex'
import { Notify } from 'vant'
import { getProductDetail } from '@/api/productDetail'
export default {
  name: 'sumbit-index',
  data () {
    return {
      address: {
        first: '联系人',
        second: '地址'
      },
      shippingFee: 20,
      selectedPaymentMethod: 'creditCard',
      sellerMessage: '',
      coupons: {
        available: [],
        disabled: [],
        chosenCoupon: -1
      },
      showList: false,
      payment: {
        actions: [{ name: '微信' }, { name: '余额' }, { name: '支付宝' }, { name: 'payPal' }, { name: '银行卡' }],
        showPayment: false
      },
      buynow: {
        product: {
          goods_image: '',
          goods_price_min: 0
        },
        goodsId: '',
        goodsNum: '',
        goodsSku: ''
      }
    }
  },
  methods: {
    goBack () {
      this.$router.go(-1)
    },
    async submitOrder () {
      // 提交订单逻辑
      // 简单验证
      if (this.sellerMessage === '' || this.sellerMessage === undefined) {
        this.sellerMessage = '尽快发货'
      }
      if (this.buynow.goodsId === '') {
        const allIds = this.cartIdsAll.map(Element => Element).join(',')
        // console.log('all ids:', allIds)
        const res = await submitOrderInCartApi('10', '0', 0, 'cart', '10', this.sellerMessage, allIds)
        // console.log(res)
        if (res.status === 200) {
          this.$router.replace('/cart')
        }
      } else {
        // console.log(typeof this.buynow.goodsId, typeof this.buynow.goodsNum, typeof this.buynow.goodsSku)
        const res = await submitOrderBuyNow('10', '0', 0, 'buyNow', 10, this.sellerMessage, this.buynow.goodsId, this.buynow.goodsNum, this.buynow.goodsSku)
        // console.log(res)
        if (res.status === 200) {
          this.$router.replace('/cart')
        }
      }
    },
    handleClickAddress () {
      this.$router.push('/addresses')
    },
    onChange (index) {
      this.showList = false
      this.coupons.chosenCoupon = index
    },
    onExchange (code) {
      // this.coupons.push(coupon)
    },
    onSelect (item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      // console.log(item)
      this.payment.showPayment = false
    }
  },
  computed: {
    ...mapGetters('cart', ['selectedItems', 'totalPrice', 'cartIdsAll']),
    payableAmount () {
      let couponFee = this.coupons.available[this.coupons.chosenCoupon]
      if (couponFee !== undefined) {
        couponFee = couponFee.value / 100
      } else {
        couponFee = 0
      }
      // console.log('chosen coupon:', this.coupons.available[this.coupons.chosenCoupon], 'couponFee:', couponFee)
      return this.total + this.shippingFee - couponFee
    },
    total () {
      if (this.buynow.goodsId === '') {
        return this.totalPrice
      }
      return this.buynow.product.goods_price_min * this.buynow.goodsNum
    }
  },
  async created () {
    if (this.$route.query.goodsId !== undefined) {
      // console.log(this.$route.query.goodsId, this.$route.query.goodsNum, this.$route.query.goodsSku)
      this.buynow = {
        goodsId: this.$route.query.goodsId,
        goodsNum: this.$route.query.goodsNum,
        goodsSku: this.$route.query.goodsSku
      }
      const { data: { detail } } = await getProductDetail(this.$route.query.goodsId)
      this.buynow.product = detail
      // console.log('product:', this.buynow.product)
    } else if (this.selectedItems.length === 0) {
      Notify('订单取消')
      this.$router.go(-1)
    }
    const defaultAddress = await getDefaultAddressApi()
    if (defaultAddress.data.defaultId === 0) {
      Notify({
        message: '你还没有收货地址-<',
        type: 'warning'
      })
      this.$router.replace('/addresses')
      return
    }
    // console.log(defaultAddress.data.defaultId)
    const res = await getAddressDetailApi(defaultAddress.data.defaultId)
    // console.log(res)
    this.address.first = res.data.detail.name + ' ' + res.data.detail.phone
    this.address.second = res.data.detail.region.province + res.data.detail.region.city + res.data.detail.region.region + ' ' + res.data.detail.detail
    // console.log(this.address)
    // console.log('selectedItems:', this.selectedItems)
    this.coupons.available = [
      {
        available: 1,
        condition: '无使用门槛\n最多优惠12元',
        reason: '',
        value: 150,
        name: '优惠券名称',
        startAt: 1489104000,
        endAt: 1514592000,
        valueDesc: '1.5',
        unitDesc: '元'
      }
    ]
  }
}
</script>

<style scoped>
.order-checkout {
  font-family: Arial, sans-serif;
}
.header {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #f8f8f8;
  border-bottom: 1px solid #ddd;
}

.back-button {
  background: none;
  border: none;
  font-size: 16px;
}

.title {
  font-size: 18px;
}

.section-title {
  font-size: 16px;
  margin: 10px 0;
}

.address,
.order-summary,
.seller-message {
  padding: 10px;
}
.address{
  background-color: #ededed;
  position: relative;
}
.address .van-icon{
  position: absolute;
  right: 8px;
  top: 40px;
}
.item{
    /* border: 1px solid black; */
    height: 110px;
  }
.item img{
    /* border: 1px solid black; */
    position: absolute;
    left: 5px;
    width: 100px;
  }
.item .product{
    /* border: 1px solid rgb(224, 162, 162); */
    position: absolute;
    right: 0;
    left: 120px;
    height: 100px;
  }
.item .product .des {
    font-size: 12px;
    height: 30px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
.item .product .price{
    color: red;
    font-weight: bold;
    font-size: 16px;
    position:absolute;
    bottom: 5px;
    /* left: 150px; */
  }
.item .product .quatity{
    /* background-color: #e18a8a; */
    font-size: 20px;
    color: #958b8b;
    position: absolute;
    right: 10px;
  }
.total-amount,
.shipping-fee,
.discount,
.payable-amount {
  font-size: 16px;
  margin: 5px 0;
}
.message-input {
  width: 100%;
  height: 100px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.footer {
  padding: 10px;
  background-color: #f8f8f8;
  border-top: 1px solid #ddd;
}

.submit-button {
  width: 100%;
  padding: 10px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
}
</style>
