import request from '@/utils/request'
export function getAddressesListApi () {
  return request.get('/address/list')
}
export function getRegionTreeApi () {
  return request.get('/region/tree')
}
export function getDefaultAddressApi () {
  return request.get('/address/defaultId')
}
export function addAddressApi (name, phone, region, detail) {
  return request.post('/address/add', {
    form: {
      name,
      phone,
      region,
      detail
    }
  })
}
export function setDefaultAddressApi (addressId) {
  return request.post('/address/setDefault', {
    addressId
  })
}
export function getAddressDetailApi (addressId) {
  return request.get('/address/detail', {
    params: {
      addressId
    }
  })
}
export function updateAddressApi (addressId, name, phone, region, detail) {
  return request.post('/address/edit', {
    addressId,
    form: {
      name,
      phone,
      region,
      detail
    }
  })
}
export function deleteAddressApi (addressId) {
  return request.post('/address/remove', {
    addressId
  })
}
